<template>
  <el-container class="project">
    <el-aside width="200px" class="select-project">
      <button @click="dialogToggle"></button>
      <el-menu :default-active="0">
        <el-menu-item v-for="(item,index) in newsLists" :key="index" :index="index" @click="showdetail(item)">
          <span slot="title">{{item.subjectName}}</span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main>
      <el-row :gutter="20">
        <el-col :span="24">
          <div class="grid-content bg-purple">
            <div class="article-header">
              <div class="article-title">{{newsList.subjectName}}</div>
              <div class="article-info clearfix">
                <div class="article-info-left">
                  <span>{{newsList.subjectTime}}</span><span>|</span>
                  <span>{{newsList.knowGroupTypeValue}}</span><span>|</span>
                  <span>{{newsList.knowTypeValue}}</span>
                </div>
                <div class="article-info-right">
                  <span class="edit-project" @click="edit()"><i class="fa fa-edit"></i>&nbsp;</span><span>|</span>
                  <span class="del-project" @click="deleteProject() "><i class="fa fa-trash-o"></i>&nbsp;</span>
                </div>
              </div>
            </div>
            <div class="article-content">
              <div class="know-article">
                <div class="know-articles"><br>
                  <p>{{newsList.subjectDest}}</p>
                </div>
                <div class="know-articles" v-html="''+'<br>'+'<p>'+this.newsList.subjectRemark+'</p>'"></div>
              </div>
              <div class="article-attachs">
                <span class="tag-name"></span>
                <!-- <span class="tag-content" v-show="typeof this.newsList.attaches === 'undefined'||articleInfo.attaches.length == 0"></span>
                <span class="attach-group" v-for="(item,index) in articleInfo.attaches" @click="previewAttach(item)"> {{item.attachName}}</span> -->
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-main>
    <el-dialog title="" :visible.sync="dialogFormVisible" size="mini" class="main-content" width="70%">
      <el-form :label-position="labelPosition" :inline="true" label-width="150px" :rules="rules" :model="addDetail" size="mini" ref="formName">
        <el-form-item label="" prop="subjectName">
          <el-input v-model="addDetail.subjectName" placeholder="" name="subjectName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectTime">
          <el-input v-model.number="addDetail.subjectTime" placeholder="" name="subjectTime"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectDest">
          <el-input v-model="addDetail.subjectDest" placeholder="" name="subjectDest"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectType">
          <el-select v-model="addDetail.subjectType" placeholder="">
            <el-option v-for="item in subjectType" :key="item.typeId" :label="item.typeValue" :value="item.typeId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="subjectGroupType">
          <el-select v-model="addDetail.subjectGroupType" placeholder="">
            <el-option v-for="item in subjectGroupType" :key="item.typeId" :label="item.typeValue" :value="item.typeId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="subjectRemark" style="height:calc(100% - 200px)" class="subjectledge-content-editor">
          <div style="height:100%">
            <textarea name="editor" id="editor"></textarea>
          </div>
        </el-form-item>
        <el-upload class="upload-demo clearfix" ref="upload" action="www.baidu.com" :on-remove="handleRemove" :file-list="fileList" :auto-upload="false">
          <div class="updateweekly"></div>
          <el-button slot="trigger" size="small" type="primary" class="selfile"></el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
        </el-upload>
      </el-form>
      <span slot="footer" class="dialog-footer">
              <el-button @click="addProject(addDetail,'formName',fileList)" size="small"></el-button>
              <el-button type="primary" @click="dialogFormVisible= false" size="small"></el-button>
            </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisible" width="70%" size="mini" class="main-content">
      <el-form :label-position="labelPosition" :inline="true" label-width="130px" :rules="rules" :model="editDetail" size="mini" ref="formName">
        <el-form-item label="" prop="subjectName">
          <el-input v-model="editDetail.subjectName" placeholder="" name="subjectName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectTime">
          <el-input v-model.number="editDetail.subjectTime" placeholder="" name="subjectTime"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectDest">
          <el-input v-model="editDetail.subjectDest" placeholder="" name="subjectDest"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectType">
          <el-select v-model="editDetail.subjectType" placeholder="">
            <el-option v-for="item in subjectType" :key="item.typeId" :label="item.typeValue" :value="item.typeId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="subjectGroupType">
          <el-select v-model="editDetail.subjectGroupType" placeholder="">
            <el-option v-for="item in subjectGroupType" :key="item.typeId" :label="item.typeValue" :value="item.typeId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="subjectRemarks" style="height:calc(100% - 200px)" class="subjectledge-content-editors">
          <div style="height:100%">
            <textarea name="editor" id="edit"></textarea>
          </div>
        </el-form-item>
        <el-upload class="upload-demo clearfix" ref="upload" action="www.baidu.com" :on-remove="handleRemove" :file-list="fileList" :auto-upload="false">
          <div class="updateweekly"></div>
          <el-button slot="trigger" size="small" type="primary" class="selfile"></el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload(editDetail, filesList)"></el-button>
        </el-upload>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button @click="update(editDetail,'formName',filesList)" size="small"></el-button>
          <el-button type="primary" @click="dialogVisible = false" size="small"></el-button>
        </span>
    </el-dialog>
  </el-container>
</template>
<script>
  import service from "../scripts/service.js";
  require("kindeditor");
  require("kindeditor/themes/default/default.css");
  export default {
    name: "table1",
    data() {
      return {
        activeIndex: 8,
        list: [{
            subjectId: 1,
            subjectName: ""
          },
          {
            subjectId: 2,
            subjectName: ""
          },
          {
            subjectId: 3,
            subjectName: ""
          },
          {
            subjectId: 4,
            subjectName: ""
          }
        ],
        addDetail: {
          subjectType: "",
          subjectGroupType: ""
        },
        editDetail: {},
        filesList: [{
            attachName: "",
            attachRemark: "www.baidu.com"
          },
          {
            attachName: "",
            attachRemark: "www.baidu.com"
          }
        ],
        subjecttype: [],
        index: 0,
        dialogVisible: false,
        dialogFormVisible: false,
        newsList: {},
        newsLists: {},
        fileList: [{
            attachName: "",
            attachRemark: ""
          },
          {
            attachName: "",
            attachRemark: ""
          }
        ],
        //
        multipleSelection: [],
        rules: {
          subjectGroupType: [{
            required: true,
            message: "",
            trigger: "blur"
          }],
          subjectName: [{
            required: true,
            message: "",
            trigger: "blur"
          }],
          subjectTime: [{
              required: true,
              message: "",
              trigger: "blur"
            },
            {
              type: "number",
              message: ""
            }
          ],
          subjectDest: [{
            required: true,
            message: "",
            trigger: "blur"
          }],
          subjectType: [{
            required: true,
            message: "",
            trigger: "blur"
          }]
        }
      };
    },
    mounted: function() {
      this.getAllList();
      this.getselect();
    },
    methods: {
      dialogToggle() {
        new Promise((resolve, reject) => {
          this.dialogFormVisible = true;
          resolve("success");
        }).then(() => {
          this.initEdit();
        });
      },
      //
      showdetail(item) {
        let params = {
          subjectId: item.subjectId
        };
        console.log(params);
        service
          .getProjectDetail(params)
          .then(res => {
            console.log(res);
            // list
            this.newsList = res.data;
            console.log(this.newsList);
            this.editDetail = {
              subjectName: this.newsList.subjectName,
              subjectTime: this.newsList.subjectTime,
              subjectType: this.newsList.subjectType,
              subjectGroupType: this.newsList.subjectGroupType,
              subjectDest: this.newsList.subjectDest
            };
            console.log(this.editDetail);
          })
          .catch(err => {
            console.log(err);
          });
        console.log(item.subjectId);
      },
      initEdit() {
        KindEditor.create("#editor", {
          width: "90%",
          height: this.editorHeight + "px",
          themeType: "simple",
          items: [
            "undo",
            "redo",
            "|",
            "selectall",
            "formatblock",
            "fontname",
            "fontsize",
            "|",
            "forecolor",
            "hilitecolor",
            "bold",
            "italic",
            "underline",
            "strikethrough",
            "lineheight",
            "removeformat",
            "|",
            "preview",
            "cut",
            "copy",
            "paste",
            "plainpaste",
            "|",
            "justifyleft",
            "justifycenter",
            "justifyright",
            "justifyfull",
            "insertorderedlist",
            "insertunorderedlist",
            "indent",
            "outdent",
            "subscript",
            "superscript",
            "|",
            "image",
            "hr",
            "code"
          ],
          resizeType: "",
          newlineTag: "br",
          imageTabIndex: 1,
          uploadJson: "/KnowledgesController/savePic",
          filePostName: "file",
          allowImageUpload: true,
          allowFileManager: false,
          formatUploadUrl: true,
          allowImageRemote: false,
          afterUpload: function(url) {
            //console.log(url)
          }
        });
        KindEditor.create("#edit", {
          width: "90%",
          height: this.editorHeight + "px",
          themeType: "simple",
          items: [
            "undo",
            "redo",
            "|",
            "selectall",
            "formatblock",
            "fontname",
            "fontsize",
            "|",
            "forecolor",
            "hilitecolor",
            "bold",
            "italic",
            "underline",
            "strikethrough",
            "lineheight",
            "removeformat",
            "|",
            "preview",
            "cut",
            "copy",
            "paste",
            "plainpaste",
            "|",
            "justifyleft",
            "justifycenter",
            "justifyright",
            "justifyfull",
            "insertorderedlist",
            "insertunorderedlist",
            "indent",
            "outdent",
            "subscript",
            "superscript",
            "|",
            "image",
            "hr",
            "code"
          ],
          resizeType: 1,
          newlineTag: "br",
          imageTabIndex: 1,
          uploadJson: "/KnowledgesController/savePic",
          filePostName: "file",
          allowImageUpload: true,
          allowFileManager: false,
          formatUploadUrl: true,
          allowImageRemote: false,
          afterUpload: function(url) {
            //console.log(url)
          }
        });
      },
      getselect() {
        service
          .getListGroupType()
          .then(res => {
            this.subjectGroupType = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        service
          .getListType()
          .then(res => {
            this.subjectType = res.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      //
      getAllList() {
        service
          .getAllList()
          .then(res => {
            console.log(res);
            // list
            this.newsLists = res.data;
            console.log(this.newsLists);
            this.newsList = this.newsLists[0];
            // (this.activeIndex = this.newsList.subjectId),
            console.log(this.activeIndex);
            this.editDetail = {
              subjectName: this.newsList.subjectName,
              subjectTime: this.newsList.subjectTime,
              subjectType: this.newsList.subjectType,
              subjectGroupType: this.newsList.subjectGroupType,
              subjectDest: this.newsList.subjectDest
            };
            console.log(this.editDetail);
          })
          .catch(err => {
            console.log(err);
          });
      },
      //
      addProject(formName, fileList) {
        let subjectRemark = KindEditor.instances[0]
          .html()
          .split("\n")
          .join("");
        console.log(subjectRemark);
        this.$refs["formName"].validate(valid => {
          if (valid) {
            this.addDetail = {
              subjectRemark: subjectRemark,
              subjectName: this.addDetail.subjectName,
              subjectTime: this.addDetail.subjectTime,
              subjectType: this.addDetail.subjectType,
              subjectGroupType: this.addDetail.subjectGroupType,
              subjectDest: this.addDetail.subjectDest
            };
            let params = {
              csl: this.addDetail,
              cslas: this.fileList
            };
            console.log(params);
            service
              .addTogether(params)
              .then(response => {
                alert("submit!");
                this.getAllList();
              })
              .catch(error => {
                console.log(error);
              });
            this.dialogFormVisible = false;
          } else {
            return false;
          }
        });
      },
      //
      deleteProject() {
        this.$confirm("")
          .then(_ => {
            let params = {
              subjectId: this.newsList.subjectId
            };
            console.log(params);
            service
              .delOneCheckSubject(params)
              .then(response => {
                console.log(response);
                // 
                this.getAllList();
              })
              .catch(error => {
                console.log(error);
              });
          })
          .catch(_ => {});
      },
      //
      edit() {
        new Promise((resolve, reject) => {
          this.dialogVisible = true;
          resolve("success");
        }).then(() => {
          this.initEdit();
          KindEditor.instances[0].html(this.newsList.subjectRemark);
          console.log(this.newsList.subjectRemark);
        });
      },
      //
      update(subjectId, formName) {
        let subjectRemarks = KindEditor.instances[0]
          .html()
          .split("\n")
          .join("");
        console.log(subjectRemarks);
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.editDetail = {
              subjectId: this.newsList.subjectId,
              subjectRemark: subjectRemarks,
              subjectName: this.editDetail.subjectName,
              subjectTime: this.editDetail.subjectTime,
              subjectType: this.editDetail.subjectType,
              subjectGroupType: this.editDetail.subjectGroupType,
              subjectDest: this.editDetail.subjectDest
            };
            let params = {
              csl: this.editDetail
            };
            console.log(params);
            service
              .getUpdate(params)
              .then(response => {
                this.showdetail();
              })
              .catch(error => {
                console.log(error);
              });
            this.dialogVisible = false;
            alert("submit!");
          } else {
            return false;
          }
        });
      },
      submitUpload(editDetail, filesList) {
        this.$refs.upload.submit();
        let params = {
          subjectId: this.editDetail.subjectId,
          csla: this.filesList
        };
        console.log(params);
        service
          .addAttachInfo(params)
          .then(response => {})
          .catch(error => {
            console.log(error);
          });
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      }
    }
  };
</script>

<style scoped>
  .project {
    position: absolute;
    top: 0px;
    bottom: 0;
    left: 0;
    right: 0;
    min-width: 1150px;
  }
  .el-menu-item,
  .el-submenu__title,
  .el-submenu .el-menu-item {
    height: 40px !important;
    line-height: 40px !important;
    color: #666666 !important;
    font-size: 14px;
  }
  .el-aside>div,
  .el-aside>div>ul {
    height: 100%;
  }
  .el-menu-item.is-active {
    color: #666666 !important;
    border-right: 3px solid rgba(24, 144, 255, 1);
    background-color: rgba(230, 247, 255, 1);
  }
  .select-project button {
    background: #008cd5;
    border: 0;
    cursor: pointer;
    padding: 9px 18px;
    border-radius: 3px;
    color: #fff;
    margin: 20px 44px;
  }
  .del-project {
    color: red;
    cursor: pointer;
  }
  .edit-project {
    color: #008cd5;
    cursor: pointer;
  }
  .dialog-footer {
    margin-top: 20px !important;
    margin-right: 40% !important;
    margin-bottom: 20px !important;
  }
  .updateweekly {
    margin-top: 5px;
    float: left;
  }
  .bg-purple {
    background: #ffffff;
  }
  .grid-content {
    border-radius: 4px;
    color: #333333;
  }
  .article-header {
    color: #333333;
    padding: 20px 40px 0px 30px;
  }
  .article-title {
    font-size: 24px;
  }
  .article-info {
    color: #a1a1a1;
    font-size: 12px;
    margin: 15px 0px 15px 0px;
  }
  .article-info span {
    margin-right: 10px;
  }
  .article-info-left {
    float: left;
  }
  .article-info-right {
    float: right;
  }
  .article-content {
    color: #666666;
    padding: 0px 40px 14px 30px;
  }
  .know-article {
    padding: 15px 0;
    font-size: 14px;
    margin-bottom: 20px;
  }
  .know-articles {
    padding-bottom: 20px;
  }
  .article-attachs {
    padding-top: 15px;
    border-top: 1px #eeeeee solid;
  }
  .updateweekly {
    margin-top: 5px;
    float: left;
  }
  .selfile {
    margin-left: 10px !important;
  }
  /* .el-form-item__content {
    float: right;
  } */
</style>


